.wordSearch {
    display: flex;
    flex-direction: column;
    align-items: stretch;

    margin: 1rem 0.4rem;

    font-size: 1rem;
}

.wordSearchRow {
    display: flex;
}

.wordSearchRow + .wordSearchRow > .wordSearchCol {
    border-top: none;
}

.wordSearchCol {
    display: flex;
    justify-content: center;

    min-width: 1.5rem;
    width: 100%;

    border: 1px solid var(--color-text-black);
}

.wordSearchCol + .wordSearchCol {
    border-left: none;
}